import React, {Component} from 'react';
import {Form, Input, Modal, Select, message, Icon, Upload} from "antd";
import {http, Durian} from "../../../utils";
import {reqAmmeterList, reqCraftCheckedList, reqDeviceTypeList, reqFindCenterList} from "../../../api";

const _ = require('lodash')

const {Option} = Select;

const formItemLayout = {
    labelCol: {
        xs: {span: 24},
        sm: {span: 6},
    },
    wrapperCol: {
        xs: {span: 24},
        sm: {span: 14},
    },
};

function getBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

class _AddEditDeviceModal extends Component {

    state = {
        visibility: false,
        previewVisible: false,
        previewImage: '',
        fileList: [
            // {
            //     uid: '-1',
            //     name: 'xxx.png',
            //     status: 'done',
            //     url: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png',
            // },
        ],

        craftList: [], // 
        workCenterList: [], // 
        machineTypeList: [], // 
        meterList: [],// 
    };

    componentWillMount() {
        console.log('componentWillMount called', this.props.selectedDeviceObj)
        this.setState({
            visibility: this.props.addDeviceModalVisible,
        })

        // if (this.props.selectedDeviceObj.devicePic) {
        //     this.setState({
        //         fileList: [this.props.selectedDeviceObj.devicePic],
        //     })
        // } else {
        //     this.setState({
        //         fileList: [],
        //     })
        // }
    }

    async componentDidMount() {
        //I
        const companyCode = Durian.get('user').companyCode
        const res1 = await reqCraftCheckedList(companyCode)
        if (res1 && res1.code === 200) {
            let originalContent = res1.data;
            const craftList = originalContent.map((item, index) => {
                return {
                    key: item.firstGroupCode,// key
                    value: item.firstGroupCode, // 
                    label: item.firstGroupName, // 
                }
            })
            this.setState({
                craftList,
            })
        } else if (res1 && res1.code === 1010) {
            // message.warn('')
        } else {
            message.error('')
        }

        //II
        const res2 = await reqFindCenterList()
        if (res2 && res2.code === 200) {
            const originalContent = res2.data;
            const workCenterList = originalContent.map((content, index) => {
                return {
                    key: content.id,// key
                    value: content.workCenterCode, // 
                    label: content.workCenterName, // 
                }
            })

            this.setState({
                workCenterList,
            })
        } else if (res2 && res2.code === 1010) {
            // message.warn('')
        } else {
            message.error('')
        }

        //III
        const res3 = await reqDeviceTypeList({
            "pageNum": 0,
            "pageSize": 0,
            "query": {}
        })
        if (res3 && res3.code === 200) {
            const originalContent = res3.data.list;
            if (!originalContent.length) return;
            const machineTypeList = originalContent.map((content, index) => {
                return {
                    key: content.id,// key
                    value: content.typeCode, // 
                    label: content.typeName, // 
                }
            })

            this.setState({
                machineTypeList
            })
        } else if (res3 && res3.code === 1010) {
            // message.warn('')
        } else {
            message.error('')
        }

        //IV
        const res4 = await reqAmmeterList()
        if (res4 && res4.code === 200) {
            const originalContent = res4.data;
            const meterList = originalContent.map((content, index) => {
                return {
                    key: content.id,// key
                    value: content.deviceId, // 
                    label: content.deviceId // 
                }
            })

            this.setState({
                meterList,
            })
        } else {
            // message.error('')
        }
    }

    componentWillReceiveProps(nextProps, nextContext) {
        console.log('componentWillReceiveProps called', nextProps.selectedDeviceObj)
        if (nextProps.addDeviceModalVisible !== this.props.addDeviceModalVisible) {
            this.setState({
                visibility: nextProps.addDeviceModalVisible,
            })
            if (nextProps.addDeviceModalVisible) {
                const {
                    craftList, // 
                    workCenterList, // 
                    machineTypeList, // 
                } = this.state
                let warnText = ''
                if (craftList.length === 0) {
                    warnText += ''
                }
                if (workCenterList.length === 0) {
                    warnText += ''
                }
                if (machineTypeList.length === 0) {
                    warnText += ''
                }

                if (warnText !== '') {
                    message.warn(` ${warnText} `)
                }
            }
            // if (nextProps.selectedDeviceObj.devicePic) {
            //     this.setState({
            //         fileList: [nextProps.selectedDeviceObj.devicePic],
            //     })
            // } else {
            //     this.setState({
            //         fileList: [],
            //     })
            // }
        }
    }

    // modal
    handleCancel = () => {
        console.log('Clicked cancel button');
        this.setState({
            fileList: [], // 
            visibility: false,
        });
        if (this.props.onCancelClickedListener) {
            this.props.onCancelClickedListener()
        }
    }

    // form
    handleSubmit = (e) => {
        e.preventDefault();
        const {form, selectedDeviceObj} = this.props
        // console.log('selectedDeviceObj', selectedDeviceObj)
        const deviceKey = _.get(selectedDeviceObj, 'key')
        const {fileList, craftList, workCenterList, machineTypeList} = this.state
        form.validateFields(async (err, values) => {
            console.log('handleSubmit called!', values)
            // console.log('craftList', craftList)
            // console.log('workCenterList', workCenterList)
            // console.log('machineTypeList', machineTypeList)
            if (!err) {
                // 
                this.setState({
                    visible: true,
                    confirmLoading: true,
                });

                let requestUrl = '/machineInfo/saveElectricityInfo'

                let formData = new FormData()

                // id
                if (deviceKey) {
                    formData.append('id', selectedDeviceObj.id) // 
                }

                formData.append('firstGroupName', craftList.filter(item => item.value === values.belongingTechnique)[0].label) // map
                formData.append('firstGroupCode', values.belongingTechnique) // 

                formData.append('workCenterName', workCenterList.filter(item => item.value === values.belongingCenterName)[0].label) // map
                formData.append('workCenterCode', values.belongingCenterName) // 

                formData.append('plcType', values.plcType) // 

                formData.append('machineTypeName', machineTypeList.filter(item => item.value === values.deviceType)[0].label) // map
                formData.append('machineTypeCode', values.deviceType) // 

                formData.append('machineNo', values.deviceNumber) // 
                formData.append('machineName', values.deviceName) // 
                formData.append('machineModel', values.deviceModel) // 
                formData.append('dataUnit', values.deviceUnit) // 

                if (fileList.length>0) {
                    formData.append('imgFile', fileList[0].originFileObj) // 
                }
                formData.append('isFlag', '1') // 

                const result = await http.postFormData(requestUrl, formData)

                // console.log('handleSubmit result', result)

                if (result.code === 200) {
                    if (this.props.onOkClickedListener) {
                        this.props.onOkClickedListener()
                    }
                    message.success('')
                } else {
                    message.error(``)
                }
                this.setState({
                    visible: false,
                    confirmLoading: false,
                    previewImage: '',
                    fileList: [],
                });
            } else {
                message.error('')
            }
        });
    }

    // 
    handlePreviewCancel = () => this.setState({previewVisible: false});

    // 
    handlePreview = async file => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }

        this.setState({
            previewImage: file.url || file.preview,
            previewVisible: true,
        });
    };

    // 
    handleChange = ({fileList, file, event}) => {
        console.log('handleChange called', fileList, file, event)
        this.setState({fileList})
    };

    render() {
        const {form, selectedDeviceObj, plcTypeList} = this.props
        const {confirmLoading} = this.state
        console.log('===render===', selectedDeviceObj)
        const deviceKey = _.get(selectedDeviceObj, 'key')
        const belongingTechnique = _.get(selectedDeviceObj, 'belongingTechnique') // 
        const belongingCenterName = _.get(selectedDeviceObj, 'belongingCenterName') // 
        const plcType = _.get(selectedDeviceObj, 'plcType') // PLC
        const deviceType = _.get(selectedDeviceObj, 'deviceTypeName') // 
        const deviceNumber = _.get(selectedDeviceObj, 'deviceNumber') // 
        const deviceName = _.get(selectedDeviceObj, 'deviceName') // 
        const deviceModel = _.get(selectedDeviceObj, 'deviceModel') // 
        const deviceUnit = _.get(selectedDeviceObj, 'deviceUnit') // 
        const meterListString = _.get(selectedDeviceObj, 'electricityStr') // 
        const devicePicUrl = _.get(selectedDeviceObj,'devicePic.url') // url

        const {
            getFieldDecorator, getFieldError, isFieldTouched,
        } = form

        const {visibility, previewVisible, previewImage, fileList, craftList, machineTypeList, workCenterList, meterList} = this.state

        // const belongingTechniqueError = isFieldTouched('belongingTechnique') && getFieldError('belongingTechnique'); // 
        // const belongingCenterNameError = isFieldTouched('belongingCenterName') && getFieldError('belongingCenterName'); // 
        // const deviceTypeError = isFieldTouched('deviceType') && getFieldError('deviceType'); // 
        // const deviceNumberError = isFieldTouched('deviceNumber') && getFieldError('deviceNumber'); // 
        // const deviceNameError = isFieldTouched('deviceName') && getFieldError('deviceName'); // 
        // const deviceModelError = isFieldTouched('deviceModel') && getFieldError('deviceModel'); // 
        // const deviceUnitError = isFieldTouched('deviceUnit') && getFieldError('deviceUnit'); // 
        // const meterRelatedError = isFieldTouched('meterRelated') && getFieldError('meterRelated'); // 
        // const devicePicError = isFieldTouched('devicePic') && getFieldError('devicePic'); // 

        const uploadButton = (
            <div>
                <Icon type="plus"/>
                <div className="ant-upload-text"></div>
            </div>
        );

        console.log('this.state', this.state)
        return (
            <Modal
                title={deviceKey ? '' : ''}
                width='50%'
                bodyStyle={{width: '100%'}}
                visible={visibility}
                onOk={this.handleSubmit}
                confirmLoading={confirmLoading}
                onCancel={this.handleCancel}
                destroyOnClose={true}
            >
                <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                    <Form.Item
                        label=":"
                        // hasFeedback
                    >
                        {getFieldDecorator('belongingTechnique',
                            craftList.length > 0 && craftList.findIndex(item=>item.label === belongingTechnique) >-1
                                ? {
                                    rules: [{required: true, message: '!'}],
                                    initialValue: craftList.filter(item => item.label === belongingTechnique)[0].value,
                                }
                                : {
                                    rules: [{required: true, message: '!'}],
                                }
                        )(
                            <Select
                                placeholder=""
                                autoClearSearchValue={true}
                                showSearch
                                optionFilterProp="children"
                                filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                            >
                                {
                                    craftList.map(role => {
                                        return (
                                            <Option
                                                key={role.key}
                                                value={role.value}
                                            >{role.label}</Option>
                                        )
                                    })
                                }
                            </Select>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=":"
                        // hasFeedback
                    >
                        {getFieldDecorator('belongingCenterName',
                            workCenterList.length > 0 && workCenterList.findIndex(item=>item.label === belongingCenterName) >-1
                                ? {
                                    rules: [{required: true, message: '!'}],
                                    initialValue: workCenterList.filter(item => item.label === belongingCenterName)[0].value,
                                }
                                : {
                                    rules: [{required: true, message: '!'}],
                                })(
                            <Select
                                placeholder=""
                                autoClearSearchValue={true}
                                showSearch
                                optionFilterProp="children"
                                filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                            >
                                {
                                    workCenterList.map(role => {
                                        return (
                                            <Option
                                                key={role.key}
                                                value={role.value}
                                            >{role.label}</Option>
                                        )
                                    })
                                }
                            </Select>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=":"
                        // hasFeedback
                    >
                        {getFieldDecorator('deviceType',
                            machineTypeList.length > 0 && machineTypeList.findIndex(item=>item.label === deviceType) >-1
                                ? {
                                    rules: [{required: true, message: '!'}],
                                    initialValue: machineTypeList.filter(item => item.label === deviceType)[0].value,
                                }
                                : {
                                    rules: [{required: true, message: '!'}],
                                })(
                            <Select
                                placeholder=""
                                autoClearSearchValue={true}
                                showSearch
                                optionFilterProp="children"
                                filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                            >
                                {
                                    machineTypeList.map(role => {
                                        return (
                                            <Option
                                                key={role.key}
                                                value={role.value}
                                            >{role.label}</Option>
                                        )
                                    })
                                }
                            </Select>
                        )}
                    </Form.Item>
                    <Form.Item
                        label="plc:"
                        // hasFeedback
                    >
                        {getFieldDecorator('plcType',
                            plcTypeList.length > 0 && plcTypeList.findIndex(item=>parseInt(item.value) === parseInt(plcType)) >-1
                                ? {
                                    rules: [{required: true, message: 'plc!'}],
                                    initialValue: plcTypeList.filter(item => parseInt(item.value) === parseInt(plcType))[0].value,
                                }
                                : {
                                    rules: [{required: true, message: 'plc!'}],
                                })(
                            <Select
                                placeholder="plc"
                                autoClearSearchValue={true}
                                showSearch
                                optionFilterProp="children"
                                filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                            >
                                {
                                    plcTypeList.map(role => {
                                        return (
                                            <Option
                                                key={role.key}
                                                value={role.value}
                                            >{role.label}</Option>
                                        )
                                    })
                                }
                            </Select>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=":"
                    >
                        {getFieldDecorator('deviceNumber', {
                            rules: [{required: true, message: '!'}],
                            initialValue: deviceNumber
                        })(
                            <Input
                                style={{width: '100%'}}
                                placeholder=""/>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=":"
                    >
                        {getFieldDecorator('deviceName', {
                            rules: [{required: true, message: '!'}],
                            initialValue: deviceName
                        })(
                            <Input
                                style={{width: '100%'}}
                                placeholder=""/>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=":"
                    >
                        {getFieldDecorator('deviceModel', {
                            rules: [{required: true, message: '!'}],
                            initialValue: deviceModel
                        })(
                            <Input
                                style={{width: '100%'}}
                                placeholder=""/>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=":"
                    >
                        {getFieldDecorator('deviceUnit', {
                            rules: [{required: true, message: '!'}],
                            initialValue: deviceUnit
                        })(
                            <Input
                                style={{width: '100%'}}
                                placeholder=""/>
                        )}
                    </Form.Item>

                    <Form.Item
                        label=":"
                        // hasFeedback
                    >
                        {getFieldDecorator('devicePic', {
                            // 
                            rules: [{required: (!deviceKey || (deviceKey && !devicePicUrl)), message: '!'}],
                        })(
                            <div>
                                <Upload
                                    className={'upload-style'}
                                    // action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                                    accept={'.jpeg,.jpg,.png,.bmp'}
                                    listType={'picture-card'}
                                    fileList={fileList}
                                    onPreview={this.handlePreview}
                                    onChange={this.handleChange}
                                >
                                    {fileList.length >= 1 ? null : uploadButton}
                                </Upload>
                                <Modal
                                    title={''}
                                    visible={previewVisible}
                                    footer={null}
                                    onCancel={this.handlePreviewCancel}>
                                    <img alt="example" style={{width: '100%'}} src={previewImage}/>
                                </Modal>
                            </div>
                        )}
                    </Form.Item>
                </Form>
            </Modal>
        );
    }
}

export const AddEditDeviceModal = Form.create()(_AddEditDeviceModal);