import React, {Component} from 'react';
import {Form, Input, Modal, Select, message} from "antd";
import {validStateList, http} from "../../../utils";

const _ = require('lodash')

const {Option} = Select;

const formItemLayout = {
    labelCol: {
        xs: {span: 24},
        sm: {span: 6},
    },
    wrapperCol: {
        xs: {span: 24},
        sm: {span: 14},
    },
};

class _AddPartModal extends Component {
    state = {
        visibility: false,
        partTypeList: [],
        machineList: [],

        machineNumber: '',
        partTypeCode: '',
    }

    componentWillMount() {
        const {addPartModalVisible} = this.props
        this.setState({
            visibility: addPartModalVisible,
        })
    }

    async componentDidMount() {
        //
        const result = await http.post('/productType/find/all', {
            pageNum: 0,
            pageSize: 0,
            query: {
                isFlag: '',
                partTypeName: '',
            }
        })
        if (result && result.code === 200) {
            let partTypeList = result.data.list.map(item => ({
                key: `part_${item.partTypeCode}`,
                value: item.partTypeCode,
                label: item.partTypeName
            }))
            this.setState({
                partTypeList,
            })
        } else {
            message.error('')
        }

        // 
        const result1 = await http.post('/machineInfo/findlist', {
            pageNum: 0,
            pageSize: 0,
            query: {}
        })
        if (result1.code === 200) {
            let machineList = result1.data.list.map(item => ({
                key: `machine_${item.machineNo}`,
                value: item.machineNo,
                label: item.machineName
            }))
            this.setState({
                machineList,
            })
        } else {
            message.error('')
        }
    }

    componentWillReceiveProps(nextProps, nextContext) {
        if (nextProps.addPartModalVisible !== this.props.addPartModalVisible) {
            const {addPartModalVisible, selectedPartObj} = nextProps
            const machineNumber = _.get(selectedPartObj, 'machineNumber', null) // 
            const partTypeCode = _.get(selectedPartObj, 'partTypeCode', null) // 

            this.setState({
                visibility: addPartModalVisible,
                machineNumber,
                partTypeCode,
            })
        }
    }

    handleCancel = () => {
        console.log('Clicked cancel button');
        this.setState({
            visibility: false,
            machineNumber: '', // state
            partTypeCode: '', // state
        });
        if (this.props.onCancelClickedListener) {
            this.props.onCancelClickedListener()
        }
    }

    // 
    handleSubmit = (e) => {
        e.preventDefault();
        const {form, selectedPartObj} = this.props
        const {partTypeList, machineList, partTypeCode, machineNumber} = this.state
        // console.log('selectedPartObj', selectedPartObj)
        const partKey = _.get(selectedPartObj, 'key') // 
        const id = _.get(selectedPartObj, 'id') // 
        form.validateFields(async (err, values) => {
            console.log('handleSubmit values', values)
            if (!err) {
                // 
                let params = {}
                let requestUrl = '/productInfo/save'

                this.setState({
                    visible: true,
                    confirmLoading: true,
                });

                params = Object.assign({}, params, {
                    partTypeCode,// 
                    partTypeName: partTypeList.filter(partType => partType.value === values.partTypeName)[0].label,// 
                    // partTypeName: 'I',// ()
                    partCode: values.partCode, // 
                    partName: values.partName,// 
                    machineNo: machineNumber, // 
                    machineName: machineList.filter(partType => partType.value === values.machineName)[0].label,// 
                    // machineName: 'I',// ()
                })

                // 
                if (partKey) {
                    params = Object.assign({}, params, {
                        id,// ID
                        isFlag: values.isValid,// 
                    })
                }

                const result = await this.callNetworkRequest({
                    requestUrl,
                    params,
                    requestMethod: 'POST'
                })

                console.log('handleSubmit result', result)
                // return;
                if (result && result.code === 200) {
                    if (this.props.onOkClickedListener) {
                        this.props.onOkClickedListener()
                    }
                    message.success('')
                    this.setState({
                        visible: false,
                        confirmLoading: false,
                        machineNumber: '', // state
                        partTypeCode: '', // state
                    });
                } else if (result && result.code === 1011) {
                    message.warning(result.msg)
                } else {
                    message.error(``)
                }
            } else {
                message.error('')
            }
        });
    }

    // 
    callNetworkRequest = async ({requestUrl, params, requestMethod}) => {
        let result
        if (requestMethod === 'POST') {
            result = await http.post(requestUrl, params)
        } else {
            result = await http.get(requestUrl)
        }
        console.log(`request: ${requestUrl}`, 'params:', params, 'result:', result)
        return result
    }

    render() {
        const {form, selectedPartObj} = this.props
        const {confirmLoading} = this.state
        const partKey = _.get(selectedPartObj, 'key')

        const machineName = _.get(selectedPartObj, 'machineName') // 
        const partTypeName = _.get(selectedPartObj, 'partTypeName') // 
        const partCode = _.get(selectedPartObj, 'partCode', null) // 
        const partName = _.get(selectedPartObj, 'partName', null) // 

        const isValid = _.get(selectedPartObj, 'isValid') // 

        const {
            getFieldDecorator, getFieldError, isFieldTouched,
        } = form

        const {visibility, machineList, partTypeList, machineNumber, partTypeCode} = this.state

        const machineNameError = isFieldTouched('machineName') && getFieldError('machineName'); // 
        const partTypeNameError = isFieldTouched('partTypeName') && getFieldError('partTypeName'); // 
        const partCodeError = isFieldTouched('partCode') && getFieldError('partCode'); // 
        const partNameError = isFieldTouched('partName') && getFieldError('partName'); // 
        const isValidError = isFieldTouched('isValid') && getFieldError('isValid'); // 

        return (
            <Modal
                title={partKey ? '' : ''}
                visible={visibility}
                onOk={this.handleSubmit}
                confirmLoading={confirmLoading}
                onCancel={this.handleCancel}
                destroyOnClose={true}
            >
                <Form {...formItemLayout} onSubmit={this.handleSubmit}>

                    <Form.Item
                        label=":"
                        // hasFeedback
                    >
                        {getFieldDecorator('machineName',
                            machineList.length > 0 && machineList.findIndex(item => item.label === machineName) > -1
                                ? {
                                    rules: [{required: true, message: '!'}],
                                    initialValue: machineList.filter(item => item.label === machineName)[0].value,
                                }
                                : {
                                    rules: [{required: true, message: '!'}],
                                }
                        )(
                            <Select
                                placeholder=""
                                autoClearSearchValue={true}
                                showSearch
                                optionFilterProp="children"
                                filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                                onChange={value => {
                                    // console.log('value', value)
                                    this.setState({
                                        machineNumber: value
                                    })
                                }}
                            >
                                {
                                    machineList.map(role => {
                                        return (
                                            <Option
                                                key={role.key}
                                                value={role.value}
                                            >{role.label}</Option>
                                        )
                                    })
                                }
                            </Select>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=":"
                    >
                        <div style={{lineHeight:'40px'}}>{machineNumber || '--'}</div>
                    </Form.Item>
                    <Form.Item
                        label=":"
                        // hasFeedback
                    >
                        {getFieldDecorator('partTypeName',
                            partTypeList.length > 0 && partTypeList.findIndex(item => item.label === partTypeName) > -1
                                ? {
                                    rules: [{required: true, message: '!'}],
                                    initialValue: partTypeList.filter(item => item.label === partTypeName)[0].value,
                                }
                                : {
                                    rules: [{required: true, message: '!'}],
                                }
                        )(
                            <Select
                                placeholder=""
                                autoClearSearchValue={true}
                                showSearch
                                optionFilterProp="children"
                                filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                                onChange={value => {
                                    // console.log('value', value)
                                    this.setState({
                                        partTypeCode: value
                                    })
                                }}
                            >
                                {
                                    partTypeList.map(role => {
                                        return (
                                            <Option
                                                key={role.key}
                                                value={role.value}
                                            >{role.label}</Option>
                                        )
                                    })
                                }
                            </Select>
                        )}
                    </Form.Item>
                    <Form.Item
                        label=":"
                    >
                        <div style={{lineHeight:'40px'}}>{partTypeCode || '--'}</div>
                    </Form.Item>
                    <Form.Item
                        label=":"
                    >
                        {getFieldDecorator('partName', {
                            rules: [{required: true, message: '!'}],
                            initialValue: partName
                        })(
                            <Input
                                style={{width: '100%'}}
                                placeholder=""/>
                        )}
                    </Form.Item>

                    <Form.Item
                        label=":"
                    >
                        {getFieldDecorator('partCode', {
                            rules: [{required: true, message: '!'}],
                            initialValue: partCode
                        })(
                            <Input
                                disabled={!!partKey}
                                style={{width: '100%'}}
                                placeholder=""/>
                        )}
                    </Form.Item>

                    {
                        partKey && (
                            <Form.Item
                                label=":"
                                // hasFeedback
                            >
                                {getFieldDecorator('isValid', {
                                    rules: [{required: true, message: '!'}],
                                    initialValue: isValid,
                                })(
                                    <Select
                                        placeholder=""
                                        autoClearSearchValue={true}
                                    >
                                        {
                                            validStateList.map(role => {
                                                return (
                                                    <Option
                                                        key={role.key}
                                                        value={role.value}
                                                    >{role.label}</Option>
                                                )
                                            })
                                        }
                                    </Select>
                                )}
                            </Form.Item>
                        )
                    }
                </Form>
            </Modal>
        );
    }
}

export const AddEditPartModal = Form.create()(_AddPartModal);