import React, {Component} from 'react';
import {Form, Input, InputNumber, Modal, Select, message} from "antd";
import {validStateList, http} from "../../../utils";

const _ = require('lodash')

const {Option} = Select;

const formItemLayout = {
    labelCol: {
        xs: {span: 24},
        sm: {span: 8},
    },
    wrapperCol: {
        xs: {span: 24},
        sm: {span: 14},
    },
};

class _AddStepModal extends Component {
    state={
        visibility:false,
    }

    componentWillMount() {
        this.setState({
            visibility: this.props.addStepModalVisible
        })
    }

    componentWillReceiveProps(nextProps, nextContext) {
        if (nextProps.addStepModalVisible !== this.props.addStepModalVisible) {
            this.setState({
                visibility: nextProps.addStepModalVisible
            })
        }
    }

    handleCancel = () => {
        console.log('Clicked cancel button');
        this.setState({
            visibility: false,
        });
        if (this.props.onCancelClickedListener) {
            this.props.onCancelClickedListener()
        }
    }

    // 
    handleSubmit = (e) => {
        e.preventDefault();
        const {form, selectedProcedureObj} = this.props
        const procedureKey = _.get(selectedProcedureObj, 'procedureKey') // 
        const id = _.get(selectedProcedureObj, 'id') // 
        const {
            // 
            machineName,
            machineNumber,
            partTypeCode,
            partTypeName,
            partCode,
            partName,

            // fields
            // procedureMinimumDuration,
            // procedureName,
            // procedureNumber,
            // procedureProduceDuration,
            // stepName,
            // isValid
        } = selectedProcedureObj
        console.log('selectedPartObj', selectedProcedureObj)
        form.validateFieldsAndScroll(async (err, values) => {
            if (!err) {
                console.log('handleSubmit values', values)
                // 
                let params = {}
                let requestUrl = '/productProcesses/save'

                this.setState({
                    visible: true,
                    confirmLoading: true,
                });

                params = Object.assign({}, params, {
                    machineNo:machineNumber,
                    machineName,
                    partTypeCode,
                    partTypeName,
                    partCode,
                    partName,

                    procedureName: values.stepName,
                    processName: values.procedureName,
                    processNo: values.procedureNumber,
                    nomalTime: values.procedureProduceDuration,
                    shortTime: values.procedureMinimumDuration,
                })

                // id
                if (procedureKey) {
                    params =  Object.assign({}, params, {
                        id,
                        isFlag: values.isValid, // 
                    })
                }

                const result = await this.callNetworkRequest({
                    requestUrl,
                    params,
                    requestMethod: 'POST'
                })

                console.log('handleSubmit result',result)
                if (result && result.code === 200) {
                    if (this.props.onOkClickedListener) {
                        this.props.onOkClickedListener()
                    }
                    message.success('')
                } else {
                    message.error(``)
                }
                this.setState({
                    visible: false,
                    confirmLoading: false,
                });
            } else {
                message.error('')
            }
        });
    }

    // 
    callNetworkRequest = async ({requestUrl, params, requestMethod}) => {
        let result
        if (requestMethod === 'POST') {
            result = await http.post(requestUrl, params)
        } else {
            result = await http.get(requestUrl)
        }
        console.log(`request: ${requestUrl}`, 'params:', params, 'result:', result)
        return result
    }

    render() {
        const {form, selectedProcedureObj} = this.props
        const stepKey = _.get(selectedProcedureObj, 'procedureKey')
        const partName = _.get(selectedProcedureObj, 'partName', null) // 

        const stepName = _.get(selectedProcedureObj, 'stepName', null) // 
        const procedureNumber = _.get(selectedProcedureObj, 'procedureNumber', null) // 
        const procedureName = _.get(selectedProcedureObj, 'procedureName', null) // 
        const procedureProduceDuration = _.get(selectedProcedureObj, 'procedureProduceDuration') // 
        const procedureMinimumDuration = _.get(selectedProcedureObj, 'procedureMinimumDuration') // 
        const isValid = _.get(selectedProcedureObj, 'isValid') // 

        const {
            getFieldDecorator, getFieldError, isFieldTouched,
        } = form

        const {visibility, confirmLoading} = this.state

        const stepNameError = isFieldTouched('stepName') && getFieldError('stepName'); // 
        const procedureNumberError = isFieldTouched('procedureNumber') && getFieldError('procedureNumber'); // 
        const procedureNameError = isFieldTouched('procedureName') && getFieldError('procedureName'); // 
        const procedureProduceDurationError = isFieldTouched('procedureProduceDuration') && getFieldError('procedureProduceDuration'); // 
        const procedureMinimumDurationError = isFieldTouched('procedureMinimumDuration') && getFieldError('procedureMinimumDuration'); // 
        const isValidError = isFieldTouched('isValid') && getFieldError('isValid'); // 

        return (
            <Modal
                title={stepKey?'':''}
                visible={visibility}
                onOk={this.handleSubmit}
                confirmLoading={confirmLoading}
                onCancel={this.handleCancel}
                destroyOnClose={true}
            >
                <Form {...formItemLayout} onSubmit={this.handleSubmit}>

                    <Form.Item
                        label=":"
                    >
                        <div>{partName}</div>
                    </Form.Item>

                    <Form.Item
                        label=":"
                    >
                        {getFieldDecorator('stepName', {
                            rules: [{required: true, message: '!'}],
                            initialValue: stepName
                        })(
                            <Input
                                style={{width:'100%'}}
                                placeholder=""/>
                        )}
                    </Form.Item>

                    <Form.Item
                        label=":"
                    >
                        {getFieldDecorator('procedureNumber', {
                            rules: [{required: true, message: '!'}],
                            initialValue: procedureNumber
                        })(
                            <Input
                                style={{width:'100%'}}
                                placeholder=""/>
                        )}
                    </Form.Item>

                    <Form.Item
                        label=":"
                    >
                        {getFieldDecorator('procedureName', {
                            rules: [{required: true, message: '!'}],
                            initialValue: procedureName
                        })(
                            <Input
                                style={{width:'100%'}}
                                placeholder=""/>
                        )}
                    </Form.Item>

                    <Form.Item
                        label="():"
                    >
                        {getFieldDecorator('procedureProduceDuration', {
                            rules: [{required: true, message: '!'}],
                            initialValue: procedureProduceDuration
                        })(
                            <InputNumber
                                style={{width:'100%'}}
                                placeholder=""
                                min={1}
                            />
                        )}
                    </Form.Item>

                    <Form.Item
                        label="():"
                    >
                        {getFieldDecorator('procedureMinimumDuration', {
                            rules: [{required: true, message: '!'}],
                            initialValue: procedureMinimumDuration
                        })(
                            <InputNumber
                                style={{width:'100%'}}
                                placeholder=""
                                min={1}
                            />
                        )}
                    </Form.Item>

                    {
                        stepKey && (
                            <Form.Item
                                label=":"
                                // hasFeedback
                            >
                                {getFieldDecorator('isValid', {
                                    rules: [{required: true, message: '!'}],
                                    initialValue: isValid,
                                })(
                                    <Select
                                        placeholder=""
                                        autoClearSearchValue={true}
                                    >
                                        {
                                            validStateList.map(role => {
                                                return (
                                                    <Option
                                                        key={role.key}
                                                        value={role.value}
                                                    >{role.label}</Option>
                                                )
                                            })
                                        }
                                    </Select>
                                )}
                            </Form.Item>
                        )
                    }
                </Form>
            </Modal>
        );
    }
}

export const AddEditStepModal = Form.create()(_AddStepModal);